section {
  padding-bottom: 50px;
}
/* TESTINGAL -> This affects to all sections, set custom class*/

.rowContainer {
  display: flex;
  flex-direction: row;
  /* flex-wrap: wrap; */

  padding: 1.5rem;
}

.element1 {
  width: 25%;
  padding: 0.5rem;
}

/* TESTINGAL -> This affects to all img, set custom class*/
img {
  width: 100%;
}

/* TESTINGAL -> This affects to all iframe, set custom class*/
iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.element2 {
  width: 50%;
  padding: 0.5rem;
}

.videoPlayerContainer {
  padding: 56.96% 0 0 0;
  position: relative;
}

.element3 {
  width: 25%;
  padding: 0.5rem;
}

.infoSection {
  padding-top: 0.5rem;
}

.infoSection h6 {
  font-size: 0.8rem;
  font-weight: 400;
}
